body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: rgb(241, 241, 241);
}

@media only screen and (min-width: 600px) {
  .col-33 {
    width: 31%;
    float: left;
    margin: 1%;
  }
}
ul.tabs {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333333;

    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
}

li {
    float: left;
}

li span a {
    display: block;
    color: white;
    text-align: center;
    padding: 10px;
    text-decoration: none;
}

li span a:hover {
    background-color: #111111;
    cursor: pointer;
}

li span.active {
    background-color: #555555;
}

.main-content {
    margin-top: 30px;
    padding: 20px;
}

a.is-active {
    color: pink;
    font-weight: bold;
}
.news-container {
    float: left;
    box-sizing: border-box;
    width: 100%;

    margin-bottom: 30px;

    border-radius: 5px;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.05);

    background-color: white;
}

.preview img {
    width: 100%;
    border-radius: 5px;
}

.news-content {
    text-align: justify;
}

.news-content {
    padding: 15px;
}

.description, .title-container {
    margin-bottom: 10px;
    color: #8e8e95;
}

.title-container .source {
    font-size: 12px;
    font-style: italic;
    color: #8e8e95;
}

.title-container .title {
    text-decoration: none;
    color: black;

    font-size: 20px;

    font-family: 'Anton', sans-serif;
    font-weight: 400;
    font-style: normal;
}

.footer {
    height: 10px;
}

.read-more {
    font-size: 12px;
    float: left;
}

.author {
    font-size: 12px;
    float: right;

    max-width: 40%;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.author .author-name {
    font-weight: bold;
    color: black;
}
.loader {
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -100px 0 0 -100px;
}
